/*--------------------------------------------------------------
# Content Image Overlap

1. Mobile
2. Desktop lg
3. Desktop xl
--------------------------------------------------------------*/
.uds-image-overlap {
  position: relative;
  padding-top: 1.5rem;
  width: 100%; }
  .uds-image-overlap:before {
    content: '';
    height: 1.5rem;
    width: calc(100% - 4rem);
    background-color: #ffc627;
    position: absolute;
    left: 2rem;
    top: 0; }
  .uds-image-overlap .content-wrapper {
    background-color: #ffffff;
    padding: 40px;
    overflow: hidden; }

/*------------------------------------------------------------------
2. Desktop, large
------------------------------------------------------------------- */
@media (min-width: 992px) {
  .uds-image-overlap {
    max-height: 100%;
    margin: 0 12px;
    display: grid;
    grid-template-columns: 1.5rem 1fr 20rem 10rem 1.5rem;
    grid-template-rows: 4.5rem 1fr 4.5rem;
    gap: 0px 0px; }
    .uds-image-overlap:before {
      display: none; }
    .uds-image-overlap img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      grid-column: 2 / span 2;
      grid-row: 1 / span 3; }
    .uds-image-overlap .content-wrapper {
      grid-column: 3 / span 2;
      grid-row: 2 / span 1; }
    .uds-image-overlap:after {
      content: '';
      width: 1.5rem;
      background-color: #ffc627;
      grid-column: 1 / 2;
      grid-row: 2 / span 1; }
